import PropTypes from 'prop-types';

// @mui
import { Box, Grid, Typography, useTheme } from '@mui/material';

// third-party
import { keys, includes } from 'lodash';

// project
import PageContainer from 'layouts/PageContainer';
import PageHeader from 'layouts/PageHeader';
import MainCard from 'components/cards/MainCard';

const ColorBlock = (props) => {
  const { paletteName, bgcolor, sx } = props;
  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        width: 1,
        maxWidth: 176,
        height: 52,
        p: 1,
        bgcolor,
        color:
          includes(paletteName, 'dark') ||
            includes(paletteName, 'darker') ||
            includes(paletteName, 'main')
            ? 'grey.100'
            : 'grey.700',
        ...sx
      }}
    >
      <Typography variant="inherite">{paletteName}</Typography>
      <Typography variant="subtitle2">{bgcolor}</Typography>
    </Box>
  );
};

const TextBlock = (props) => {
  const theme = useTheme();
  const { paletteName, bgcolor, sx } = props;
  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        width: 1,
        maxWidth: 176,
        height: 52,
        p: 1,
        bgcolor,
        color: theme.palette.mode === 'light' ? 'grey.100' : 'grey.700',
        ...sx
      }}
    >
      <Typography variant="subtitle2">{paletteName}</Typography>
      <Typography variant="subtitle2">{bgcolor}</Typography>
    </Box>
  );
};

TextBlock.propTypes = {
  bgcolor: PropTypes.string,
  paletteName: PropTypes.string,
  sx: PropTypes.oneOfType([
    PropTypes.arrayOf(
      PropTypes.oneOfType([PropTypes.func, PropTypes.object, PropTypes.bool])
    ),
    PropTypes.func,
    PropTypes.object
  ])
};

const UIColors = () => {
  const theme = useTheme();
  const mainColor = [
    'primary',
    'secondary',
    'info',
    'success',
    'warning',
    'error'
  ];
  return (
    <PageContainer>
      <PageHeader
        title="Colors"
        subtitle=" Colors provides the following palette colors "
      />
      <MainCard>
        <Grid container spacing={2}>
          {keys(theme.palette).filter((color => includes(mainColor, color))).map((colors, index) => (

            // includes(mainColor, colors) ? (
            <Grid key={index} item xs={6} md={4} lg={2} >
              <ColorBlock
                paletteName={`${colors}`}
                bgcolor={theme.palette[colors]['main']}
                sx={{ height: 80, fontSize: '1.215rem' }}
              />
              {keys(theme.palette[colors]).filter(color => color !== 'contrastText').map(
                (color, index) =>
                  // color !== 'contrastText' && (
                  <ColorBlock
                    key={index}
                    paletteName={`${color}`}
                    bgcolor={theme.palette[colors][color]}
                  />
                // )
              )}
            </Grid>
            // ) : undefined

          ))}
        </Grid>
      </MainCard>
      <MainCard title="Text" >
        {/* <Typography variant='h4' mt={5} mb={2}> Text</Typography> */}
        <Box
          gap={1}
          sx={{
            display: 'flex',
            justifyContent: 'flex-start',
            flexWrap: 'wrap',
            p: 1,
            m: 1
          }}
        >
          <Grid container spacing={2}>
            <Grid item xs={6} md={4}  >
              <TextBlock
                paletteName="primary"
                bgcolor={theme.palette.text.primary}
                color
              />
            </Grid>
            <Grid item xs={6} md={4}  >
              <TextBlock
                paletteName="secondary"
                bgcolor={theme.palette.text.secondary}
                color
              />
            </Grid>
            <Grid item xs={6} md={4}  >
              <TextBlock
                paletteName="disabled"
                bgcolor={theme.palette.text.disabled}
              />
            </Grid>
          </Grid>
        </Box>
      </MainCard>
    </PageContainer >
  );
};

export default UIColors;
